/* Общий стиль игры */
.game {
  text-align: center;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin-top: 40px;
  color: #333;
}

/* Доска */
.board {
  display: inline-block;
  border: 4px solid #333;
  border-radius: 12px;
  background: linear-gradient(145deg, #f0f0f0, #e0e0e0);
  padding: 10px;
  box-shadow: 6px 6px 15px rgba(0,0,0,0.2), -6px -6px 15px rgba(255,255,255,0.7);
}

/* Ряд */
.row {
  display: flex;
}

/* Клетка */
.square {
  width: 70px;
  height: 70px;
  font-size: 32px;
  margin: 5px;
  cursor: pointer;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f0f0f0;
  transition: all 0.2s ease;
  box-shadow: inset 2px 2px 5px rgba(0,0,0,0.1), inset -2px -2px 5px rgba(255,255,255,0.7);
}

.square:hover {
  transform: scale(1.1);
  box-shadow: inset 1px 1px 4px rgba(0,0,0,0.2), inset -1px -1px 4px rgba(255,255,255,0.9);
}

/* Красивые крестики и нолики */
.square.x {
  color: #e74c3c;
  font-weight: bold;
}

.square.o {
  color: #3498db;
  font-weight: bold;
}

/* Кнопка сброса */
.reset {
  margin-top: 25px;
  padding: 10px 20px;
  cursor: pointer;
  background: #3498db;
  color: white;
  font-size: 16px;
  border: none;
  border-radius: 8px;
  box-shadow: 3px 3px 6px rgba(0,0,0,0.2);
  transition: background 0.2s ease, transform 0.2s ease;
}

.reset:hover {
  background: #2980b9;
  transform: scale(1.05);
}

/* --- Адаптив для мобильных экранов --- */
@media (max-width: 500px) {
  .square {
    width: 50px;
    height: 50px;
    font-size: 24px;
    margin: 3px;
  }

  .reset {
    padding: 8px 16px;
    font-size: 14px;
  }

  .board {
    padding: 6px;
  }
}

/* Адаптив для планшетов */
@media (max-width: 768px) {
  .square {
    width: 60px;
    height: 60px;
    font-size: 28px;
    margin: 4px;
  }

  .reset {
    padding: 9px 18px;
    font-size: 15px;
  }
}
